class Produto {
    constructor (nome, preco) {
        this.nome = nome;
        this.preco = preco;
    }

    material (material) {
        return console.log(material)
    }

    cor (cor) {
        return console.log(cor)
    }
}


const p1 = new Produto ('caneca', 30);
p1.material('Porcelana');
p1.preco = 'oi';
console.log(p1.preco);